import React from "react";
import styled from 'styled-components';

const SingleMovie = styled.div`
  .single-movie {
    width: 33.3333333%;
    border: 10px solid mistyrose;
  }
`;

class PopularMovie extends React.Component {
    render() {
        return (
          <SingleMovie>
            <div className="single-movie">
              <img src={`https://image.tmdb.org/t/p/w300/${this.props.movieDetails.poster_path}`} alt="move poster" />
            </div>
          </SingleMovie>
        )
    }
}

export default PopularMovie;
